/* PRODUCT SWIPER */
.swiper-product {
  height: 100%;
  width: 100%;
  text-align: center;

  -webkit-overflow-scrolling: auto !important;
  background: gray;
}
.swiper-slide-product {
  position: relative;
  background: #323232;
}
#product-slide-1 {
  display: flex;
}
#product-slide-1-left,
#product-slide-1-right {
  position: relative;
  width: 50%;
  height: 100%;
}
.product-slide-1-img {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-size: cover;
  background-position: center;
  animation-name: imgFader;
  animation-duration: 24s;
  animation-iteration-count: infinite;
}
.product-slide-1-img:nth-child(1){
  animation-delay: 0;
}
.product-slide-1-img:nth-child(2){
  animation-delay: 6s;
}
.product-slide-1-img:nth-child(3){
  animation-delay: 12s;
}
.product-slide-1-img:nth-child(4){
  animation-delay:18s;
}
@keyframes imgFader {
  0%{opacity: 0;}
  12%{opacity: 1;}
  25%{opacity: 1;}
  37%{opacity: 0;}
  100%{opacity: 0;}
}
#product-slide-1-right {
  background: #323232;
  color: white;
  text-transform: uppercase;
}
#product-slide-1-right p {
  font-size: 3vmax;
}
#shop-now-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  grid-gap: .25em;
  gap: .25em;
}
#shop-now-btn {
  margin-top: 2vmax;
  padding: 4vmax 8vmax;
  color: white;
  background: rgba(0,0,0,.15);
  box-shadow: 
  1px 1px 4px rgba(0,0,0,.25),
  4px 4px 12px rgba(0,0,0,.25);
  transition: all 250ms ease;
}
#shop-now-btn:hover {
  background: rgba(0,0,0,.5);
  box-shadow: none;
  transition: all 250ms ease;
}
#swipe-indicator {
  position: absolute;
  right: 1vmax;
  bottom: 1vmax;
  display: flex;
  align-items: center;
}
#swipe-indicator-chevron {
  width: 4vmax;
  animation-name: swipeAnimation;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
@keyframes swipeAnimation {
  0%{opacity: 0; transform: translateX(0);}
  100%{opacity: .1; transform: translateX(20%);}
}
/* VIDEO */
/* terrifying styles incoming */
#section-video {
  position: relative;
  background-image: url(/apogee-athletic-mk2/static/media/snatch.6de4ab12.JPG);
  background-position: center top;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
}
.section-overlay {
  opacity: 0;
  transition: opacity 1s ease;
}
.section-overlay-toggle {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: rgba(0,0,0,.75);
  opacity: 1;
  transition: opacity 1s ease;
}
.play-button-off {
  transform: translateY(-50%) scale(0);
  opacity: 0;
  transition: all 350ms ease;

}
.play-button-on {
  z-index: 2;
  transform: translateY(-50%) scale(1);
  opacity: .25;
  transition: all 350ms ease;
}
.play-button-on:hover {
  transform: translateY(-50%) scale(.95);
  opacity: .1;
  cursor: pointer;
  transition: all 250ms ease;
}
.play-button-on,
.play-button-off {
  position: absolute;
  top: 50%;
  width:  50vmax;
  height: 50vmax;
  border-radius: 50%;
}
#play-button-toggle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(.5);
  width: 75vh;
  height: 75vh;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  transition: all 1s ease;
}
.feature-video,
.feature-video.show {
  position: absolute;
  top: 50%;
  width: 100vmin;
  aspect-ratio: 4 / 3;
}
.feature-video {
  transform: translateY(-50%) scale(0);
  opacity: 0;
}
.feature-video.show {
  transform: translateY(-50%) scale(1);
  border: none;
  outline: none;
  box-shadow: 0 0 4px rgba(0,0,0, 1);
  overflow: hidden;

  animation-name: videoFade;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-fill-mode: forwards;
  -webkit-animation-name: videoFade;
  -webkit-animation-duration: 1s;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
  -webkit-animation-fill-mode: forwards;
}
@keyframes videoFade {
  from { opacity: 0; }
   to   { opacity: 1; }
}
/* CONTACT */
#section-contact {
  position: relative;
  display: flex;
}
#contact-left,
#contact-right {
  width: 50%;
}
#contact-left {
  position: relative;
  background-image: url(/apogee-athletic-mk2/static/media/graphic-kara-medball.b0a6cf24.JPG);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 2px 2px 8px rgba(0,0,0,.25);
}
#signup-group {
  position: absolute;
  left: 50%;
  top: 65%;
  transform: translateX(-50%);
  width: 50%;
  padding: 2em;
  display: flex;
  flex-direction: column;
  grid-gap: 1vmax;
  gap: 1vmax;
  background: rgba(50, 50, 50, .5);
  box-shadow: 
    1px 1px 4px rgba(0,0,0,.25),
    4px 4px 12px rgba(0,0,0,.25);
}
#signup-group button,
#signup-group input {
  outline: none;
  border: none;
  padding: 1vmax;
}
#contact-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 5vmax;
  gap: 5vmax;

  background: rgb(50, 50, 50);
}
#contact-copy-group p:nth-child(1){
  font-size: 4em;
}
#contact-copy-group p:nth-child(2){}
#contact-copy-group p:nth-child(3){
  color: white;
  opacity: .1;
}
#contact-copy-group p:nth-child(3) a{
  color: white;
}
#listen-grid-perspective {
  perspective: 100px;
  perspective-origin: 20%;
}
#listen-grid {
  width: 20vmax;
  height: 20vmax;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1vmax;
  gap: 1vmax;
  transform: rotateY(8deg);
}
.listen-img,
.listen-img:hover {
  transition: all 100ms ease;
}
.listen-img {
  height: 10vmax;
  width: 10vmax;
}
.listen-img:hover {
  cursor: pointer;
  transform: scale(1.05);
}
/* FOOTER */
